* {
  box-sizing: border-box;
}

html, body {
  color: #fff;
  --border-radius-allround: 100px;
  --border-radius-notallround: 100px 100px 20px 20px;
  --blue: #002fa7;
  --brick: #bc4b33;
  --lilac: #783cf5;
  --sage: #4d837f;
  --sky: linear-gradient(180deg, #e5e5e5 0%, #002fa7 100%);
  --fancygradient: linear-gradient(180deg, #002fa7 0%, #4d837f 100%);
  --textfont: "Courier Prime", monospace;
  --titlefont: Syne;
  --gutter: 3.125vw 6.25vw;
  background: #000;
  margin: 0;
  padding: 0;
  font-size: 30px;
  font-weight: 500;
}

section {
  z-index: 2;
  width: 100%;
  height: calc(100vh - 100px);
  display: flex;
  position: relative;
  top: 100px;
}

section.sticky {
  margin-top: 100px;
  position: sticky;
}

section.full-screen {
  z-index: 3;
  height: 100vh;
  margin-top: 0;
  top: 0;
}

h1 {
  font-size: 70px;
  line-height: 1em;
  font-family: var(--titlefont);
  margin: 0;
}

h2 {
  font-size: 40px;
  font-family: var(--titlefont);
  color: var(--brick);
  margin: 0;
}

p {
  font-size: 26px;
}

a {
  color: var(--brick);
  font-size: 20px;
  text-decoration: none;
}

nav {
  font-family: var(--textfont);
  z-index: 1;
  width: 100%;
  height: 100px;
  font-size: 20px;
  position: fixed;
  top: 0;
  left: 0;
}

nav ul {
  align-items: center;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  display: flex;
}

nav ul li {
  margin-left: 45px;
  list-style: none;
}

section.about .empty {
  background: var(--sky);
  border-radius: var(--border-radius-notallround);
  flex-basis: 37.5vw;
  transition: flex-basis 2s ease-out;
}

section.about article {
  flex-direction: column;
  flex: 1;
  display: flex;
}

section.about article .title {
  background: var(--blue);
  border-radius: var(--border-radius-allround);
  font-family: var(--textfont);
  flex: 1;
  align-items: flex-end;
  padding: 3.125vw;
  transition: flex-basis 1s ease-out;
  display: flex;
}

section.about article .description {
  background: var(--brick);
  border-radius: var(--border-radius-allround);
  color: #fff;
  flex: 1;
  align-items: center;
  max-height: 60vh;
  padding: 3.125vw;
  display: flex;
}

section.service article {
  width: 100%;
  display: flex;
}

section.service figure {
  background: var(--lilac);
  border-radius: var(--border-radius-allround);
  flex-basis: 56.25vw;
  justify-content: center;
  align-items: center;
  max-width: 62.5vw;
  margin: 0;
  transition: flex-basis 4s ease-out;
  display: flex;
}

section.service figure img {
  width: 45%;
}

section.service .text {
  background: var(--sage);
  border-radius: var(--border-radius-notallround);
  flex-direction: column;
  flex-basis: 93.75vw;
  justify-content: center;
  padding: 3.125vw;
  display: flex;
}

section.service .text a {
  font-family: monospace;
}

section.service.service-projects figure {
  background-color: var(--blue);
}

section.service.service-contact figure {
  background: var(--brick);
}

nav a {
  color: #fff;
  font-family: Courier New, Courier, monospace;
}

section.email {
  background: var(--fancygradient);
  border-radius: var(--border-radius-allround);
  justify-content: center;
  align-items: center;
}

section.email a {
  color: #000;
  font-family: Syne;
  font-size: 56px;
  transition: color 2s;
}

section.email a:hover {
  color: #fff;
}

section.service.service-stories figure img {
  animation-name: floating;
  animation-duration: 2s;
  animation-timing-function: ease-in-out;
  animation-iteration-count: infinite;
  animation-direction: alternate;
}

section.service.service-projects figure img {
  animation-name: spinning;
  animation-duration: 2s;
  animation-timing-function: ease-in-out;
  animation-iteration-count: infinite;
  animation-direction: alternate;
}

section.service.service-contact figure img {
  animation-name: scaleandturn;
  animation-duration: 2s;
  animation-iteration-count: infinite;
  animation-direction: alternate;
}

@keyframes opacitychange {
  0% {
    opacity: .4;
  }

  100% {
    opacity: 1;
  }
}

@keyframes spinning {
  0% {
    transform: rotateX(40deg);
  }

  100% {
    transform: rotateX(-20deg);
  }
}

@keyframes scaleandturn {
  0% {
    filter: blur(10px);
    transform: rotateZ(180deg)scale3d(.8, .8, 1);
  }

  100% {
    filter: blur();
    transform: rotateZ(0)scale3d(1, 1, 1);
  }
}

@keyframes floating {
  0% {
    transform: translateY(-10%);
  }

  100% {
    transform: translateY(10%);
  }
}

@media only screen and (width <= 768px) {
  html, body {
    --border-radius-allround: 50px;
    --border-radius-notallround: 50px 50px 10px 10px;
  }

  .empty {
    display: none;
  }

  h1 {
    padding: 5px;
    font-size: 56px;
  }

  h2 {
    font-size: 36px;
  }

  section a {
    font-size: 16px;
  }

  nav ul li {
    margin-left: 10px;
    font-size: 11px;
  }

  section.service article {
    flex-direction: column;
  }

  section.service figure {
    flex-basis: 25vh;
    width: 100%;
  }

  section.service figure img {
    width: 30%;
  }

  p {
    font-size: 22px;
  }

  section.email a {
    font-size: 40px;
    transform: rotate(90deg);
  }
}

@media only screen and (height >= 599px) {
  h2 {
    font-size: 30px;
  }

  p {
    font-size: 18px;
  }

  @media only screen and (width >= 1200px) {
    h2 {
      font-size: 40px;
    }

    p {
      font-size: 30px;
    }
  }

  @media only screen and (width >= 1900px) {
    h2 {
      font-size: 50px;
    }

    p {
      font-size: 40px;
    }
  }
}

